import { tsUtils } from '@neo-one/ts-utils';
import ts from 'typescript';
import { ScriptBuilder } from '../../sb';
import { VisitOptions } from '../../types';
import { BuiltinInstanceMemberCall } from '../BuiltinInstanceMemberCall';
import { CallMemberLikeExpression } from '../types';

// tslint:disable-next-line export-name
export class ArrayJoin extends BuiltinInstanceMemberCall {
  public canCall(_sb: ScriptBuilder, _func: CallMemberLikeExpression, node: ts.CallExpression): boolean {
    return tsUtils.argumented.getArguments(node).length <= 1;
  }

  public emitCall(
    sb: ScriptBuilder,
    func: CallMemberLikeExpression,
    node: ts.CallExpression,
    optionsIn: VisitOptions,
    visited: boolean,
  ): void {
    const options = sb.pushValueOptions(optionsIn);
    const expr = tsUtils.expression.getExpression(func);
    if (!visited) {
      // [arrayVal]
      sb.visit(expr, options);
    }

    // [arr]
    sb.emitHelper(node, options, sb.helpers.unwrapArray);
    const args = tsUtils.argumented.getArguments(node);
    const hasJoinString = args.length > 0;
    if (hasJoinString) {
      // [val, arr]
      sb.visit(args[0], options);
      // [string, arr]
      sb.emitHelper(node, options, sb.helpers.unwrapString);
    }
    // [string]
    sb.emitHelper(node, options, sb.helpers.arrToString({ type: sb.context.analysis.getType(expr), hasJoinString }));

    if (optionsIn.pushValue) {
      // [val]
      sb.emitHelper(node, options, sb.helpers.wrapString);
    } else {
      sb.emitOp(node, 'DROP');
    }
  }
}
