import { tsUtils } from '@neo-one/ts-utils';
import ts from 'typescript';
import { Context } from '../../../Context';
import { Types } from '../../constants';
import { IsHelper } from './IsHelper';
import { UnwrapHelper } from './UnwrapHelper';
import { WrapHelper } from './WrapHelper';

export class UnwrapIterableIteratorHelper extends UnwrapHelper {}
export class WrapIterableIteratorHelper extends WrapHelper {
  protected readonly type = Types.IterableIterator;
}
export class IsIterableIteratorHelper extends IsHelper {
  protected readonly type = Types.IterableIterator;
}

export const hasIterableIterator = (context: Context, node: ts.Node, type: ts.Type): boolean =>
  tsUtils.type_.hasType(type, (tpe) => isIterableIterator(context, node, tpe));

export const isOnlyIterableIterator = (context: Context, node: ts.Node, type: ts.Type): boolean =>
  tsUtils.type_.isOnlyType(type, (tpe) => isIterableIterator(context, node, tpe));

export const isIterableIterator = (context: Context, node: ts.Node, type: ts.Type): boolean =>
  context.builtins.isInterface(node, type, 'IterableIterator') || context.builtins.isInterface(node, type, 'Iterator');
